body {
  margin: 0;
}
.App {
  font-family: sans-serif;
  padding-bottom: 20px;
}
.add-form-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.add-form-body input {
  margin-bottom: 15px;
}
.add-form-body textarea {
  margin-bottom: 15px;
}
.add-form-body input:last-child {
  margin-bottom: 0;
}
.view-title {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 10px;
}
.view-body {
  font-size: 20px;
  font-weight: 200;
  font-style: italic;
}
.form-title {
  text-align: center;
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 15px;
}
.form-body {
  display: flex;
  flex-direction: column;
}
.form-body input[type="text"] {
  padding: 10px;
  margin-bottom: 15px;
}
